"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { ArrowLeft, Calendar, MapPin, Phone, User, CheckCircle2 } from "lucide-react"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Badge } from "@/components/ui/badge"
import { createAppointment, getAppointments, deleteAppointment } from "@/lib/api"

interface Appointment {
  _id: string;
  patientName: string;
  patientPhone: string;
  village: string;
  appointmentDate: string;
  doctorType: string;
  preferredTime: string;
  symptoms: string;
  status: 'pending' | 'confirmed' | 'cancelled';
}

interface FormData {
  patientName: string;
  patientPhone: string;
  village: string;
  appointmentDate: string;
  doctorType: string;
  preferredTime: string;
  symptoms: string;
  status: 'pending' | 'confirmed' | 'cancelled';
}

export default function AppointmentsPage() {
  const [activeTab, setActiveTab] = useState<"book" | "upcoming">("book")
  const [bookingComplete, setBookingComplete] = useState(false)
  const [loading, setLoading] = useState(false)
  const [upcomingAppointments, setUpcomingAppointments] = useState<Appointment[]>([])
  const [formData, setFormData] = useState<FormData>({
    patientName: "",
    patientPhone: "",
    village: "",
    appointmentDate: "",
    doctorType: "",
    preferredTime: "",
    symptoms: "",
    status: "pending"
  })

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData((prev: FormData) => ({ ...prev, [name]: value }))
  }

  const handleSelectChange = (name: string, value: string) => {
    setFormData((prev: FormData) => ({ ...prev, [name]: value }))
  }

  const fetchAppointments = async () => {
    try {
      const data = await getAppointments();
      setUpcomingAppointments(data);
    } catch (error) {
      console.error('Error fetching appointments:', error);
    }
  };

  useEffect(() => {
    fetchAppointments();
  }, [activeTab]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    
    try {
      await createAppointment({
        patientName: formData.patientName,
        patientPhone: formData.patientPhone,
        village: formData.village,
        appointmentDate: new Date(formData.appointmentDate),
        doctorType: formData.doctorType,
        preferredTime: formData.preferredTime,
        symptoms: formData.symptoms
      });

      setBookingComplete(true)
      fetchAppointments()
    } catch (error) {
      console.error('Error booking appointment:', error)
      alert('Failed to book appointment. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  const handleCancelAppointment = async (id: string) => {
    try {
      await deleteAppointment(id);
      fetchAppointments()
    } catch (error) {
      console.error('Error cancelling appointment:', error)
      alert('Failed to cancel appointment. Please try again.')
    }
  }

  return (
    <main className="min-h-screen bg-background">
      <header className="bg-green-50 border-b">
        <div className="container mx-auto px-4 py-4 flex justify-between items-center">
          <div className="flex items-center gap-2">
            <Link href="/" className="flex items-center gap-2">
              <ArrowLeft className="h-5 w-5 text-green-600" />
              <span className="text-green-800 font-medium">Back to Home</span>
            </Link>
          </div>
          <h1 className="text-xl font-bold text-green-800">Doctor Appointments</h1>
          <div className="w-24"></div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8 max-w-4xl">
        <Tabs defaultValue={activeTab} onValueChange={(value) => setActiveTab(value as "book" | "upcoming")} className="w-full">
          <TabsList className="grid w-full grid-cols-2 mb-6">
            <TabsTrigger value="book">Book Appointment</TabsTrigger>
            <TabsTrigger value="upcoming">Upcoming Appointments</TabsTrigger>
          </TabsList>

          <TabsContent value="book">
            {bookingComplete ? (
              <Card className="border-green-100 shadow-sm">
                <CardHeader className="bg-green-50 border-b border-green-100 text-center">
                  <CheckCircle2 className="h-16 w-16 text-green-600 mx-auto mb-2" />
                  <CardTitle className="text-2xl text-green-800">Appointment Booked Successfully!</CardTitle>
                </CardHeader>
                <CardContent className="pt-6 text-center">
                  <p className="text-lg mb-6">
                    Your appointment has been scheduled. We will send a confirmation to your phone.
                  </p>
                  <div className="bg-green-50 border border-green-100 rounded-lg p-4 max-w-md mx-auto mb-6">
                    <h3 className="font-medium text-green-800 mb-2">Appointment Details:</h3>
                    <p className="text-green-700">
                      <strong>Doctor Type:</strong> {formData.doctorType}
                      <br />
                      <strong>Date:</strong> {formData.appointmentDate}
                      <br />
                      <strong>Time:</strong> {formData.preferredTime}
                      <br />
                      <strong>Location:</strong> Primary Health Center, {formData.village}
                    </p>
                  </div>
                </CardContent>
                <CardFooter className="flex flex-col sm:flex-row gap-4 justify-center">
                  <Button
                    variant="outline"
                    className="border-green-200"
                    onClick={() => {
                      setBookingComplete(false)
                      setFormData({
                        patientName: "",
                        patientPhone: "",
                        village: "",
                        appointmentDate: "",
                        doctorType: "",
                        preferredTime: "",
                        symptoms: "",
                        status: "pending"
                      })
                    }}
                  >
                    Book Another Appointment
                  </Button>
                  <Button className="bg-green-600 hover:bg-green-700" onClick={() => setActiveTab("upcoming")}>
                    View My Appointments
                  </Button>
                </CardFooter>
              </Card>
            ) : (
              <Card className="border-green-100 shadow-sm">
                <CardHeader className="bg-green-50 border-b border-green-100">
                  <CardTitle className="text-2xl text-green-800">Book a Doctor Appointment</CardTitle>
                  <CardDescription className="text-green-700">
                    Fill in your details to schedule an appointment with a healthcare provider.
                  </CardDescription>
                </CardHeader>
                <CardContent className="pt-6">
                  <form onSubmit={handleSubmit} className="space-y-6">
                    <div className="grid md:grid-cols-2 gap-6">
                      <div className="space-y-2">
                        <Label htmlFor="patientName" className="text-base">
                          <User className="h-4 w-4 inline mr-2" />
                          Full Name
                        </Label>
                        <Input
                          id="patientName"
                          name="patientName"
                          placeholder="Enter your full name"
                          className="text-base"
                          value={formData.patientName}
                          onChange={handleInputChange}
                          required
                        />
                      </div>

                      <div className="space-y-2">
                        <Label htmlFor="patientPhone" className="text-base">
                          <Phone className="h-4 w-4 inline mr-2" />
                          Phone Number
                        </Label>
                        <Input
                          id="patientPhone"
                          name="patientPhone"
                          type="tel"
                          placeholder="Enter your phone number"
                          className="text-base"
                          value={formData.patientPhone}
                          onChange={handleInputChange}
                          required
                        />
                      </div>

                      <div className="space-y-2">
                        <Label htmlFor="village" className="text-base">
                          <MapPin className="h-4 w-4 inline mr-2" />
                          Village
                        </Label>
                        <Input
                          id="village"
                          name="village"
                          placeholder="Enter your village"
                          className="text-base"
                          value={formData.village}
                          onChange={handleInputChange}
                          required
                        />
                      </div>

                      <div className="space-y-2">
                        <Label htmlFor="appointmentDate" className="text-base">
                          <Calendar className="h-4 w-4 inline mr-2" />
                          Preferred Date
                        </Label>
                        <Input
                          id="appointmentDate"
                          name="appointmentDate"
                          type="date"
                          className="text-base"
                          value={formData.appointmentDate}
                          onChange={handleInputChange}
                          required
                        />
                      </div>
                    </div>

                    <div className="space-y-2">
                      <Label className="text-base">Doctor Type</Label>
                      <Select
                        name="doctorType"
                        value={formData.doctorType}
                        onValueChange={(value) => handleSelectChange("doctorType", value)}
                      >
                        <SelectTrigger className="text-base">
                          <SelectValue placeholder="Select doctor type" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="General Medicine">General Medicine</SelectItem>
                          <SelectItem value="Pediatrics">Pediatrics</SelectItem>
                          <SelectItem value="Gynecology">Gynecology</SelectItem>
                          <SelectItem value="Dentistry">Dentistry</SelectItem>
                          <SelectItem value="Ophthalmology">Ophthalmology</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    <div className="space-y-2">
                      <Label className="text-base">Preferred Time</Label>
                      <RadioGroup
                        value={formData.preferredTime}
                        onValueChange={(value) => handleSelectChange("preferredTime", value)}
                        className="grid grid-cols-1 md:grid-cols-3 gap-4"
                      >
                        <div className="flex items-center space-x-2">
                          <RadioGroupItem value="Morning (9AM - 12PM)" id="morning" />
                          <Label htmlFor="morning">Morning (9AM - 12PM)</Label>
                        </div>
                        <div className="flex items-center space-x-2">
                          <RadioGroupItem value="Afternoon (2PM - 5PM)" id="afternoon" />
                          <Label htmlFor="afternoon">Afternoon (2PM - 5PM)</Label>
                        </div>
                        <div className="flex items-center space-x-2">
                          <RadioGroupItem value="Evening (6PM - 9PM)" id="evening" />
                          <Label htmlFor="evening">Evening (6PM - 9PM)</Label>
                        </div>
                      </RadioGroup>
                    </div>

                    <div className="space-y-2">
                      <Label htmlFor="symptoms" className="text-base">Symptoms</Label>
                      <Input
                        id="symptoms"
                        name="symptoms"
                        placeholder="Describe your symptoms"
                        className="text-base"
                        value={formData.symptoms}
                        onChange={handleInputChange}
                        required
                      />
                    </div>

                    <Button type="submit" className="w-full bg-green-600 hover:bg-green-700" disabled={loading}>
                      {loading ? "Booking..." : "Book Appointment"}
                    </Button>
                  </form>
                </CardContent>
              </Card>
            )}
          </TabsContent>

          <TabsContent value="upcoming">
            <Card className="border-green-100 shadow-sm">
              <CardHeader className="bg-green-50 border-b border-green-100">
                <CardTitle className="text-2xl text-green-800">Your Appointments</CardTitle>
                <CardDescription className="text-green-700">
                  View and manage your upcoming appointments
                </CardDescription>
              </CardHeader>
              <CardContent className="pt-6">
                {upcomingAppointments.length > 0 ? (
                  <div className="space-y-4">
                    {upcomingAppointments.map((appointment: Appointment) => (
                      <Card key={appointment._id} className="border-green-100">
                        <CardContent className="p-4">
                          <div className="flex flex-col md:flex-row justify-between">
                            <div>
                              <h3 className="font-bold text-lg text-green-800">{appointment.doctorType}</h3>
                              <div className="flex items-center gap-2 mt-2">
                                <Calendar className="h-4 w-4 text-green-600" />
                                <span>
                                  {new Date(appointment.appointmentDate).toLocaleDateString()} at {appointment.preferredTime}
                                </span>
                              </div>
                              <div className="flex items-center gap-2 mt-1">
                                <MapPin className="h-4 w-4 text-green-600" />
                                <span>{appointment.village}</span>
                              </div>
                            </div>
                            <div className="mt-4 md:mt-0 flex flex-col items-end">
                              <Badge
                                className={`mb-2 ${
                                  appointment.status === "confirmed"
                                    ? "bg-green-100 text-green-800"
                                    : appointment.status === "cancelled"
                                    ? "bg-red-100 text-red-800"
                                    : "bg-yellow-100 text-yellow-800"
                                }`}
                              >
                                {appointment.status.charAt(0).toUpperCase() + appointment.status.slice(1)}
                              </Badge>
                              {appointment.status !== "cancelled" && (
                                <Button
                                  variant="outline"
                                  className="border-red-200 text-red-600 hover:bg-red-50"
                                  onClick={() => handleCancelAppointment(appointment._id)}
                                >
                                  Cancel Appointment
                                </Button>
                              )}
                            </div>
                          </div>
                        </CardContent>
                      </Card>
                    ))}
                  </div>
                ) : (
                  <div className="text-center py-8">
                    <p className="text-gray-500">No upcoming appointments</p>
                    <Button
                      className="mt-4 bg-green-600 hover:bg-green-700"
                      onClick={() => setActiveTab("book")}
                    >
                      Book an Appointment
                    </Button>
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </main>
  )
}
